



































import Vue from "vue";

export default Vue.extend({
    props: {
        animation: {
            type: Boolean,
            default: false
        },
        animationDuration: {
            type: Number,
            default: 1000
        }
    },
    data: function () {
        return {
            showMenu: false,
            showButton: true
        }
    },
    methods: {
        openMenu() {
            this.showButton = false;
            let scope = this;
            window.setTimeout(function(){scope.showMenu = true}, this.animationDuration / 2)
        },
        closeMenu() {
            this.showMenu = false;
            let scope = this;
            window.setTimeout(function(){scope.showButton = true}, this.animationDuration / 2)
        }
    }
})
